@font-face {
  src: local(HealB), url("HealTheWebB-Regular.b4c2386d.otf");
  font-family: HealB;
  src: local(HealA), url("HealTheWebA-Regular.74330abb.otf");
  font-family: HealA;
}

:root {
  font-family: HealA;
  font-size: 62.5%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: HealA;
}

header {
  border-bottom: 1px solid #000;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu {
  margin-bottom: 2vh;
}

.dropdwn_btn {
  color: #000;
  width: 5vw;
  background: none;
  border: none;
  font-size: 4rem;
  transition: all 4s;
  animation: eye 13s infinite;
}

@keyframes eye {
  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(360deg);
  }

  100% {
    transform: rotatY(10deg);
  }
}

.dropdown_cont {
  min-height: 10.6rem;
  display: none;
  position: relative;
  left: 1rem;
}

.dropdown_cont a {
  float: none;
  color: #000;
  text-align: left;
  padding: 1.2rem 1.6rem;
  text-decoration: none;
  display: block;
}

.dropdown_cont a:hover {
  color: #fff;
  background-color: #000;
  border-radius: 68px 32px 62px 68px / 21px 67px 33px 79px;
}

.dropdown:hover .dropdown_cont {
  width: 8rem;
  display: block;
}

.dropdown:hover .dropdwn_btn {
  display: none;
}

.web {
  text-align: center;
  flex-direction: column;
  place-items: center;
  padding-top: 4rem;
  display: flex;
}

.web h2 {
  color: #00f;
  font-weight: bold;
  text-decoration: underline overline;
}

.web a {
  color: #000;
  margin-top: 1rem;
  text-decoration: none;
}

.web a:hover {
  color: #fff;
  background-color: #000;
  border-radius: 73% 27% 14% 86% / 21% 78% 22% 79%;
}

.music {
  text-align: center;
  padding-top: 2rem;
}

.music h2 {
  color: #00f;
  font-weight: bold;
  text-decoration: underline overline;
}

.music img {
  height: 4vh;
  border: none;
  margin-top: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  transition: all .5s;
}

.music a:hover img {
  height: 5.5vh;
}

@media screen and (min-width: 769px) {
  .web {
    font-size: 2rem;
  }
}

